/* You can add global styles to this file, and also import other style files */

/* Library to make things look good on all browsers*/
@import '~normalize.css';
@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@400;900&family=Source+Sans+Pro:wght@300;900&display=swap');

*,
*::after,
*::before
{
  box-sizing: border-box;
}

/* THEME SPECIFIC PROPERTIES */

:root
{
  --ff-primary: 'Source Sans Pro', sans-serif;
  --ff-secondary: 'Source Code Pro', monospace;

  --fw-reg: 300;
  --fw-bold: 900;

  --clr-light: #fff;
  --clr-dark: #303030;

  --clr-accent: #16e0bd;

  --fs-h1: 3rem;
  --fs-h2: 2.25rem;
  --fs-h3: 1.25rem;
  --fs-body: 1rem;

  --box-shadow: .25em .25em .75em rgba(0,0,0,.25),
                .125em .125em .25em rgba(0,0,0,.15);
}

@media (min-width: 800px)
{
  :root
  {
    --fs-h1: 4.5rem;
    --fs-h2: 3.75rem;
    --fs-h3: 1.5rem;

    --fs-body: 1.125rem;
  }
}

/* GENERAL STYLES */

html
{
  scroll-behavior: smooth;
}

body
{
  background-color: var(--clr-light);
  color: var(--clr-dark);
  margin: 0;
  font-family: var(--ff-primary);
  font-size: var(--fs-body);
  line-height: 1.6;
}

img
{
  display: block;
  max-width: 100%;
}

section
{
  padding: 5em 2em;
}

:focus /* When tab goes in */
{
  outline: 3px solid var(--clr-accent);
  outline-offset: 3px;
}

/* BUTTONS */

.btn
{
  display: inline-block;
  padding: .5em 2.5em;
  background: var(--clr-accent);
  color: var(--clr-dark);
  text-decoration: none;
  cursor: pointer;
  font-size: .8rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: var(--fw-bold);
  transition: transform 200ms ease-in-out;
}

.btn:hover
{
  transform: scale(1.2);
}


/* TYPOGRAPHY */
h1,h2,h3
{
  line-height: 1;
  margin: 0;
}

h1{ font-size: var(--fs-h1)}
h2{ font-size: var(--fs-h2)}
h3{ font-size: var(--fs-h3)}



strong
{
  font-weight: var(--fw-bold);
}


/* NAVIGATION */

.nav-open .nav
{
  transform: translateX(0) !important;
}

.nav-open .nav-toggle
{
  position: fixed !important;
}


.nav-open .hamburger
{
  transform: rotate(.625turn) !important;
  margin: 0 !important;
}

.nav-open .hamburger::before
{
  transform: rotate(90deg) translateX(-6px) !important;
  margin: 0 !important;
}

.nav-open .hamburger::after
{
  opacity: 0 !important;
}










